




























































import {Component, Vue} from "vue-property-decorator"
import Portal from "@/managers/portal"
import emailRegEx from "@/mixins/emailRegEx"
import PageBackground from "@/components/PageBackground.vue"
import NewCard from "./NewCard.vue"
import {VueTelInput} from "vue-tel-input"
import NotificationMessage from "@/components/NotificationMessage.vue"

@Component({ components: { PageBackground, NewCard, NotificationMessage, VueTelInput } })
export default class CreateAccount extends Vue {
	public notifyMsg: string | null = null
	private firstName: string = ""
	private lastName: string = ""
	private email: string = ""
	private password: string = ""
	private mobile: string = ""
	private acceptTerms: boolean = false
	private acceptOffers: boolean = false
	private planData: string = ""
	private number: string = "9669766616"
	private isValid: boolean = false
	private country: number = 0
	private countryCode: string = ""
	public created(): void {
		this.planData = this.$route.fullPath.split("=")[1]
		if (this.planData === "standardcamp") {
			window.localStorage.setItem("plan", this.planData)
			window.localStorage.setItem("camp", "true") }
	}
	private onInput({ country }: any): void {
			this.country = country.dialCode
			this.countryCode = this.country.toString()
		}
	private get isFormValid(): boolean {
		return this.acceptTerms &&
			this.password.trim().length >= 8 &&
			this.firstName.trim().length >= 2 &&
			this.lastName.trim().length >= 2 &&
			this.mobile.trim().length >= 10
			}
	private get isEmailValid(): boolean {
		return emailRegEx.test(this.email)
	}
	private createAccount(): void {
		window.localStorage.setItem("mobile", this.mobile)
		if (this.countryCode === "91")
			window.localStorage.setItem("countrySym", "In" )
		else if (this.countryCode === "1")
			window.localStorage.setItem("countrySym", "Us" )
		else if (this.countryCode === "45")
			window.localStorage.setItem("countrySym", "Dk")
		else window.localStorage.setItem("countrySym", "Fr")
		Portal.client.call.student.register(this.email, this.password, this.firstName, this.lastName, this.countryCode, this.mobile)
			.response
			.then(() => {
				window.localStorage.setItem("name", this.firstName)
				window.localStorage.setItem("firstTimeUser", "yes")
				Portal.client.call.subscriptions.post("subscription-a")
				.response.then()
				this.$router.replace({
					name: "signin",
					params: {email: this.email, password: this.password}
				})
			})
			.catch((reason: any) => {
				this.notifyMsg = reason
				throw reason
			})
	}

	private goBack(): void {
		this.$router.push("/")
	}
}
